@import url(https://fonts.googleapis.com/css2?family=Goldman:wght@400;700&display=swap);
* {
    box-sizing: border-box;
    margin: 0
}

.game-wrapper {
    display: flex;
    width: 100%;
    justify-content: center;
    
}


.game {
    position: relative;
    width: 800px;
    height: 800px;
    background-color: aquamarine;
    padding: 0;
    margin: 0 auto;
    line-height: 0;
    z-index: 2;
}

.empty {
    position: relative;
    display: inline-block;
    border: 1px solid rgb(255, 255, 255);
    background-color: rgb(148, 148, 148);
    align-items: center;
    justify-content: center;
    z-index: 3;
}

.snake {
    position: relative;
    display: inline-block;
    align-items: center;
    justify-content: center;
    border: 1px solid rgb(255, 255, 255);
    background-color: rgb(5, 119, 3);
    z-index: 3;
}

.food {
    position: relative;
    display: inline-block;
    align-items: center;
    justify-content: center;
    border: 1px solid rgb(255, 255, 255);
    /* background-color: rgb(95, 209, 2); */
    background-image: url('https://image.similarpng.com/very-thumbnail/2020/06/Cartoon-funny-pizza-royalty-free-PNG.png');
    background-repeat: none;
    background-size: contain;
    margin: 0;
    z-index: 3;
}

.tail {
    position: relative;
    display: inline-block;
    align-items: center;
    justify-content: center;
    border: 1px solid rgb(255, 255, 255);
    background-color: rgb(8, 168, 21);
    margin: 0;
    z-index: 3;
}

.button {
    width: 100%;
    height: 100%;
    font-size: 200px;
    border: none;
    outline: none;
    cursor: pointer;
}

.button--gameOver {
    position: absolute;
    left: 0;
    top: 0;
    z-index: 5;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.score {
    color: rgb(5, 119, 3);
    font-size: 30px;
}

.animation {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    opacity: 10%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    font-size: 60px;
    color:rgb(182, 182, 182);  
    animation: levelUp 4s linear;
    z-index: 999;
}

@keyframes levelUp {
    0% {
        color: rgb(168, 168, 168);  
        opacity: 10%;
    }
    25% {
        color: rgb(73, 73, 73);
        opacity: 40%;
    }
    50% {
        color: black;
        opacity: 40%;
    }
    80% {
        color: rgb(55, 63, 66);
        opacity: 30%;
    }
    100% {
        color: rgb(148, 148, 148);
        opacity: 10%;
    }
}
